<template>
    <div class="submitComment">
        <div class="commitHeader">
            发表评论
        </div>
        <div class="buttons">
        <button class="toggle" @click="isAnonymous = false" :class="{active:!isAnonymous}">用户留言</button>
        <button class="toggle" @click="isAnonymous = true" :class="{active:isAnonymous}">匿名留言</button>
        </div>
        <div v-if="!isAnonymous" class="userCommit commit">
            <div v-if="$store.state.token">
                你已经注册了
                {{$store.state.token}}
            </div>
            <div v-else>
                你还没注册!立即<a href="/login">登陆</a>/<a href="../../login.html">注册</a>
            </div>
        </div>
        <div v-else="isAnonymous" class="anonymousCommit commit">
            <textarea v-model="commentContent" placeholder="请输入您的高见噢">

            </textarea>
                <button type="button" @click="submitComment" class="submitBtn">
                    提交
                </button>
        </div>

    </div>
</template>

<script>

    import {post} from "../../requests/requests";

    export default {
    name: "SubmitComment",
      props:['articleId'],
    data() {
      return {
        isAnonymous: true,
        commentContent: '',
      }
    },
    methods:{
      submitComment(){
        post(
          '/comment',
          {
            commentContent: this.commentContent,
            commentArticleId:this.articleId,
            commentUserName:'吉娃娃'
          }
        ).then((result)=>{
          if(result.data.success){
            this.$emit('reloadComment')
            this.commentContent = ''
          }
        })
      }
    }
  }
</script>

<style lang="less" scoped>
    .submitComment{
        margin:0 50px;
        margin-bottom: 20px;

        .commitHeader{
            font-size:20px;
            font-weight: bold;
            margin-top: 20px;
            margin-bottom: 20px;
        }
        .buttons{
            width: 300px;
            border-bottom:1px solid #878787;
        }
        .toggle{
            border:1px solid #878787;
            background-color: #ffbcba;
            padding:10px;
            color: white;
            font-size: 16px;
            outline: none;

            &:nth-of-type(1){
                border-right: none;
                border-bottom: none;
                border-top-left-radius: 25px;
                /*border-bottom-left-radius: 5px;*/
            }
            &:nth-of-type(2){
                border-bottom:none;
                border-top-right-radius: 25px;
                /*border-bottom-right-radius: 5px;*/
            }
            &:hover{
                background-color: #83d0ff;
            }
        }
        .active{
            background-color: #83d0ff;
        }

        .commit{
            margin-top:10px;
            /*background-color: white;*/
            /*box-shadow: 0 0 3px rgba(100,100,100,.5);*/

            textarea{
                min-width: 300px;
                min-height: 100px;
            }
            .submitBtn{
                display: block;
                border:none;
                background-color: #83d0ff;
                padding:10px 18px;
                margin-left: 240px;
            }
        }
    }
    @media screen and (max-width: 800px){
        .submitComment{
            margin:0 5px;
        }
    }
</style>