<template>
    <div class="comments">
        <div class="commentTitle">
            评论区
        </div>
        <div v-if="comments">
            <comment :index="index" v-for="(comment,index) in comments" :comment="comment" :key="comment.commentId"/>
        </div>
        <submit-comment v-if="article" :article-id="article.articleId" @reloadComment="reload"/>
    </div>
</template>

<script>
    import Comment from "./Comment";
    import SubmitComment from "./SubmitComment";
    import {request} from "../../requests/requests";
    export default {
    name: "Comments",
    components:{
      Comment,
      SubmitComment
    },
    props:['article'],
    data(){
      return {
        comments:null
      }
    },
      methods:{
        reload(){
          request('/comment/'+this.article.articleId).then((result) => {
            this.comments = result.data
          })
        }
      },
    watch: {
      'article'(to, from) {
        request('/comment/'+this.article.articleId).then((result) => {
          this.comments = result.data
        })
      }
    }
  }
</script>

<style lang="less" scoped>
    .commentTitle{
        font-size: 18px;
        font-weight: bold;
        padding-bottom: 10px;
        margin:0 50px;
        border-bottom: white 3px solid;
    }

    @media screen and(max-width: 800px){
        .commentTitle{
            margin: 0 5px;
        }
        .comments{
            margin: 0 5px;
        }

    }
</style>